import { Toast } from "@/hooks/web/useToast";
// import { userStore } from '@/store/modules/user';
// const { Toast } = useMessage();

export function checkStatus(status: number, msg: string): void {
  switch (status) {
    case 400:
      Toast(`${msg}`);
      break;
    // 401: 
    // 
    // 
    case 401:
      Toast("!");
      // userStore.loginOut(true);
      break;
    case 403:
      Toast("!");
      break;
    // 404
    case 404:
      Toast(",!");
      break;
    case 405:
      Toast(",!");
      break;
    case 408:
      Toast("!");
      break;
    case 500:
      Toast(msg || ",!");
      break;
    case 501:
      Toast("!");
      break;
    case 502:
      Toast("!");
      break;
    case 503:
      Toast(msg || "!");
      break;
    case 504:
      Toast("!");
      break;
    case 505:
      Toast("http!");
      break;
    default:
  }
}
