// axios  

import type { AxiosResponse } from "axios";
import type { CreateAxiosOptions, RequestOptions, Result } from "./types";

import { VAxios } from "./Axios";
import { getLocalToken } from "@/utils/auth";
import { AxiosTransform } from "./axiosTransform";

import { checkStatus } from "./checkStatus";

import { Toast } from "@/hooks/web/useToast";

import { RequestEnum, ResultEnum, ContentTypeEnum } from "@/enums/httpEnum";

import { isString } from "@/utils/is";
import { deepMerge } from "@/utils";
import { errorServiceReport } from "@/setup/error-handle/sentry";
import { errorResult } from "./const";

/**
 * @description: 
 */
const transform: AxiosTransform = {
  /**
   * @description: 
   */
  transformRequestData: (res: AxiosResponse<Result>, options: RequestOptions) => {
    const { isTransformRequestResult } = options;
    // 
    // codedatamessage
    if (!isTransformRequestResult) {
      return res.data;
    }
    // 

    const { data } = res;
    if (!data) {
      // return '[HTTP] Request has no return value';
      return errorResult;
    }
    //   coderesultmessage  types.ts
    const { status, data: result, message } = data;
    //  status
    if (!status) {
      return data;
    }
    // 
    if (status === ResultEnum.SUCCESS) {
      return result;
    } else {
      // 
      Toast(message || ",!");
      Promise.reject(new Error(message));
      return errorResult;
    }
  },

  // config
  beforeRequestHook: (config, options) => {
    const { apiUrl } = options;

    if (apiUrl && isString(apiUrl)) {
      config.url = `${apiUrl}${config.url}`;
    }
    if (config.method === RequestEnum.GET) {
      const now = new Date().getTime();
      if (!isString(config.params)) {
        config.data = {
          //  get 
          params: Object.assign(config.params || {}, {
            _t: now,
          }),
        };
      } else {
        // restful
        config.url = config.url + config.params + `?_t=${now}`;
        config.params = undefined;
      }
    } else {
      if (!isString(config.params)) {
        config.data = config.params;
        config.params = undefined;
      } else {
        // restful
        config.url = config.url + config.params;
        config.params = undefined;
      }
    }
    return config;
  },

  /**
   * @description: 
   */
  requestInterceptors: (config) => {
    // config
    const token = getLocalToken();
    if (token) {
      //  token
      config.headers.Authorization = `Bearer user=${token}`;
    }
    return config;
  },

  /**
   * @description: 
   */
  responseInterceptorsCatch: (error: any) => {
    errorServiceReport(error); //  
    const { response, code, message } = error || {};
    // console.log(response, code, message);
    const msg: string =
      response && response.data && response.data.error ? response.data.message : "";
    const err: string = error.toString();
    try {
      if (code === "ECONNABORTED" && message.indexOf("timeout") !== -1) {
        Toast(",!");
      }
      if (err && err.includes("Network Error")) {
        Toast("");
      }
    } catch (error) {
      throw new Error(error);
    }
    checkStatus(error.response && error.response.status, msg);
    return Promise.reject(error);
  },
};

function createAxios(opt?: Partial<CreateAxiosOptions>) {
  return new VAxios(
    deepMerge(
      {
        timeout: 20 * 1000,
        // 
        // baseURL: "",
        headers: { "Content-Type": ContentTypeEnum.JSON },
        // 
        transform,
        // 
        requestOptions: {
          // 
          isTransformRequestResult: true,
          // posturl
          joinParamsToUrl: false,
          // 
          formatDate: true,
          // 
          errorMessageMode: "none",
          // 
          apiUrl: "",
        },
      },
      opt || {}
    )
  );
}
export const defHttp = createAxios({
  requestOptions: {
    apiUrl: process.env.VUE_APP_API,
  },
});

